import React, { Component } from 'react';
import * as contentful from 'contentful';

import ImageLoader from './ImageLoader';
import './App.css';

require('intersection-observer'); //polyfill

class App extends Component {
  state = {
    scenes: []
  };

  client = contentful.createClient({
    space: 'u3ytf7lup1mg',
    //this is fine
    accessToken: 'EPM2Wb7VNLA58txEAq46apY3A9HgqDIwmYnc3caPQuE'
  });

  componentDidMount() {
    this.fetchPosts().then(this.setPosts);
  }

  fetchPosts = () => this.client.getEntries({'content_type': 'scene'});

  setPosts = response => {
    this.setState({
      scenes: response.items
    });
  };

  render() {
    if (!this.state.scenes[0]) {
      return null;
    }

    return (
      <div className="app">
        <header>
          <h1>
            The Offisse
          </h1>
          <p>"High end fashion inspired by The Office" by Sharon Zheng</p>
        </header>

        <section>
          {this.state.scenes.map((scene, i) => {
            return (
              <ImageLoader
                alt={scene.fields.title}
                fields={scene.fields}
                key={`scene_${i}`}
              />
            );
          })}
        </section>
      </div>
    );
  }
}

export default App;



